.card {
	&__container {
		width: 100%;
		display: flex;
		flex-direction: column;
		align-items: center;
		&__content {
			&__name{
				font-size: 30px;
			}
		}
	}
	&__content {
			font-size: 30px;
			text-align: center;
		&__name {
			background-color: rgba(255, 255, 255, 0.2);
			text-align: center;
			border-radius: 10px;
		}
		&__age {
			font-size: 20px;
			margin-top: 15px;
		}
		&__description {
			margin-top: 15px;
			font-size: 16px;
			line-height: 1.5;
		}
		&__buttons {
			display: flex;
			column-gap: 15px;
			align-items: baseline;
			justify-content: center;
		}
	}
	&__img {
		border-radius: 50%;
			width: 200px;
			height: 200px;
			margin-bottom: 25px;
			float: left;
			object-fit: cover;
			object-position: center;
			&__container {
				width: 100%;
				display: flex;
				justify-content: center;
			}
		}
}